/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
  margin: 0;
  padding: 0;
}

.contenedor {
  display: block;
  width: 100%;
  padding: 0 10rem;
}
@media (max-width: 480px) {
  .contenedor {
    padding: 0 2rem;
  }
}
@media (max-width: 800px) {
  .contenedor {
    padding: 0 2rem;
  }
}

a {
  text-decoration: none;
}

img,
picture {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  font-weight: 300;
  text-align: center;
  color: #315e88;
  font-weight: 400;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

li {
  list-style: none;
}

button {
  display: block;
  outline: none;
  padding: 1.5rem 3rem;
  color: #ffffff;
  background: #f5841e;
  border: none;
  border-radius: 1rem;
  font-weight: 400;
  font-size: 2.5rem;
  text-transform: capitalize;
  transition: all 0.5s ease;
  opacity: 1;
  cursor: pointer;
}
button:hover {
  background: #f5841e;
  opacity: 0.8;
}

.header .bar {
  padding: 0.5rem 0;
  background-color: #f5f5f5;
}
.header .bar p {
  text-transform: uppercase;
  text-align: center;
  font-size: 1.4rem;
  color: #656565;
  font-weight: 300;
}
.header .bar p span {
  text-decoration: underline;
}

.contenido-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
@media (max-width: 800px) {
  .contenido-header {
    flex-direction: column;
  }
}
.contenido-header__title {
  display: inline-flex;
  display: flex;
}
.contenido-header__title img {
  padding-right: 1rem;
}
.contenido-header__title p {
  color: #656565;
  font-weight: 400;
}
.contenido-header__title p span {
  font-weight: 700;
  color: #f5841e;
}
@media (max-width: 800px) {
  .contenido-header__title p {
    font-size: 2rem;
  }
}
.contenido-header__call p {
  color: #656565;
  font-weight: 400;
  font-size: 2.5rem;
}
.contenido-header__call p span {
  font-weight: 700;
  color: #f5841e;
  font-size: 2.8rem;
}
@media (max-width: 800px) {
  .contenido-header__call p {
    font-size: 2rem;
  }
}

.footer {
  margin: 5rem 0 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.footer .contenedor-footer {
  width: 75rem;
}
.footer .contenedor-footer p {
  padding: 1rem 2rem;
  font-size: 1.5rem;
  line-height: 2rem;
  color: #656565;
}
.footer .contenedor-footer p span {
  text-decoration: underline;
}

.feature {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.feature ul li {
  position: relative;
}
.feature ul li p {
  text-transform: uppercase;
  font-weight: 700;
}
.feature ul li::before {
  position: absolute;
  content: "";
  left: -8%;
  top: 0.8rem;
  background: #315e88;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  opacity: 0.7;
}

.btn {
  margin: 5rem auto;
  padding: 1rem 3rem;
}

.banner {
  background: url(../img/fondo-slider.png);
  max-width: 100%;
  height: 40rem;
  border-top: 5px solid #e1e1e1;
}

.banner-img {
  background: url(../img/banner.png) left no-repeat;
  max-width: 100%;
  display: flex;
  height: 40rem;
  position: relative;
}

.banner-description {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  width: 50rem;
  align-items: flex-end;
  justify-content: center;
  flex-wrap: wrap;
  margin-right: 1rem;
  text-align: right;
}
.banner-description p {
  color: #ffffff;
  font-size: 5rem;
  line-height: 4rem;
}
.banner-description p .dollar {
  font-weight: 700;
  font-size: 10rem;
}
@media (max-width: 800px) {
  .banner-description p .dollar {
    font-size: 5rem;
  }
}
.banner-description p span {
  font-size: 3rem;
}
@media (max-width: 800px) {
  .banner-description p span {
    font-size: 2rem;
  }
}
@media (max-width: 800px) {
  .banner-description p {
    font-size: 2.8rem;
  }
}
.banner-description .btn {
  margin: 4rem 0;
}
.banner-description span {
  text-align: right;
  color: #ffffff;
  font-weight: 300;
  width: 22rem;
  line-height: 1.8rem;
}

.cards {
  display: flex;
  flex-direction: column;
  background: #fdfae9;
  padding: 3rem 0;
}

.card {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  padding: 2rem 0;
}
@media (max-width: 800px) {
  .card {
    grid-template-columns: repeat(1, 1fr);
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
    margin: auto;
  }
}
.card div {
  width: 15rem;
  color: #ffffff;
  text-align: center;
  padding: 1rem 1.5rem;
  border-radius: 1rem;
  font-size: 2rem;
  margin: 0 auto;
}
@media (max-width: 800px) {
  .card div {
    grid-template-columns: repeat(1, 1fr);
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 1rem 0;
  }
}
.card div:nth-child(1) {
  background: #d2532a;
}
.card div:nth-child(2) {
  background: #315e88;
}
.card div:nth-child(3) {
  background: #86a73e;
}